@import "../../global.scss";

.topbar {
    width: 99%;
    height: 70px;
    background-color: white;
    color: $maincolor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    
        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }
            .itemcontainer{
                display: flex;
                align-items: center;
                margin-left: 30px;
                font-size: 18px;
                margin-right: 5px;

                @include mobile {
                    display: none;
                }

                .discord{
                    background-color: red;
                    padding: 10px 7px 10px 7px; 
                    border: 0px;
                    color: white;
                    font-size: 24px;
                    font-weight: 600;
                    text-decoration: none;
                    margin-right: 0px;
                    cursor: pointer;
                }
                .opensea{
                    background-color: blue;
                    padding: 10px 7px 10px 7px;
                    border: 0px;
                    color: white;
                    font-size: 24px;
                    font-weight: 600;
                    text-decoration: none;
                    margin-right: 0px;
                    cursor: pointer;
                }
            }
        }

        .right{
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{
                    width: 100%;
                    height: 3px;
                    background-color: $maincolor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active {
        background-color: $maincolor;
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child {
                    background-color: white;
                    transform: rotate(-45deg);
                }
                
            }
        }
    }
}